import React, {Component} from 'react';
import {Breadcrumb} from 'antd';

class BreadMenu extends Component {
    render() {
        return (
            <div>
                <Breadcrumb>
                    <Breadcrumb.Item></Breadcrumb.Item>
                    <Breadcrumb.Item></Breadcrumb.Item>
                    <Breadcrumb.Item></Breadcrumb.Item>
                </Breadcrumb>
            </div>
        );
    }
}

export default BreadMenu;


