import React, {Component, PropTypes} from 'react';
import { Menu, Icon } from 'antd';
const SubMenu = Menu.SubMenu;

export default class SiderMenu extends Component {
    render() {
        return (
            <div>
                <Menu mode="inline" theme="dark"
                    defaultSelectedKeys={['1']} defaultOpenKeys={['sub1']}>
                    <SubMenu key="sub1" title={<span><Icon type="user" /></span>}>
                        <Menu.Item key="1">1</Menu.Item>
                        <Menu.Item key="2">2</Menu.Item>
                        <Menu.Item key="3">3</Menu.Item>
                        <Menu.Item key="4">4</Menu.Item>
                    </SubMenu>
                    <SubMenu key="sub2" title={<span><Icon type="laptop" /></span>}>
                        <Menu.Item key="5">5</Menu.Item>
                        <Menu.Item key="6">6</Menu.Item>
                        <Menu.Item key="7">7</Menu.Item>
                        <Menu.Item key="8">8</Menu.Item>
                    </SubMenu>
                    <SubMenu key="sub3" title={<span><Icon type="notification" /></span>}>
                        <Menu.Item key="9">9</Menu.Item>
                        <Menu.Item key="10">10</Menu.Item>
                        <Menu.Item key="11">11</Menu.Item>
                        <Menu.Item key="12">12</Menu.Item>
                    </SubMenu>
                </Menu>
            </div>
        );
    }
}


